<p><code>'join.number'</code>与<code>join</code>类似，只是会考虑它为数字的情况。</p>
<p><code>'join:,'</code>，还可以用<code>:</code>修改分隔符，类似 Vue 的指令参数。</p>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-cascade-select.vue/README.md">
    <div><anondemo-7aa389f0-1816 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span> <span class="hljs-attr">direction</span>=<span class="hljs-string">"vertical"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-cascade-select</span> <span class="hljs-attr">:value.sync</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">converter</span>=<span class="hljs-string">"join.number:|"</span> <span class="hljs-attr">:categories</span>=<span class="hljs-string">"[
        { label: 'CPU', key: 'cpu' },
        { label: '内存', key: 'memory' },
        { label: '系统盘', key: 'storage' },
    ]"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data"</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">u-cascade-select</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>选择的值为：{{ value }}<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
