<h2>API</h2>
<h3>Props/Attrs</h3>
<table>
<thead>
<tr>
<th>Prop/Attr</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>Array&lt;{ text, value }&gt;</td>
<td></td>
<td>数据列表</td>
</tr>
<tr>
<td>value.sync, v-model</td>
<td>Any</td>
<td></td>
<td>当前选择的值</td>
</tr>
<tr>
<td>categories</td>
<td>Array&lt;{ label, key, placeholder }&gt;</td>
<td></td>
<td>多级分类</td>
</tr>
<tr>
<td>hide-empty</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否隐藏空列表</td>
</tr>
<tr>
<td>converter</td>
<td>String, Object</td>
<td><code>'join'</code></td>
<td>value 与 values 的转换器。可选值：<code>'join'</code>表示将 values 数组 join 之后变成 value，<code>'join.number'</code>与<code>join</code>类似，只是会考虑它为数字的情况。也可以用<code>:</code>修改分隔符，类似 Vue 的指令参数，<code>'last-value'</code>表示以最后一项的值作为 value。也可以传入一个包含 { get, set } 的一个对象</td>
</tr>
<tr>
<td>field</td>
<td>String</td>
<td><code>'text'</code></td>
<td>显示文本字段</td>
</tr>
<tr>
<td>readonly</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否只读</td>
</tr>
<tr>
<td>disabled</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否禁用</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<h4>@input</h4>
<p>选择某一项时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event</td>
<td>Any</td>
<td>选择项的值</td>
</tr>
<tr>
<td>senderVM</td>
<td>UCascadeSelect</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h4>@select</h4>
<p>选择某一项时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.level</td>
<td>Number</td>
<td>选择的层级</td>
</tr>
<tr>
<td>$event.value</td>
<td>Any</td>
<td>改变后的值</td>
</tr>
<tr>
<td>$event.values</td>
<td>Array</td>
<td>改变后每项值的数组</td>
</tr>
<tr>
<td>$event.item</td>
<td>Object</td>
<td>选择项相关对象</td>
</tr>
<tr>
<td>$event.itemVM</td>
<td>ListViewItem</td>
<td>选择项子组件</td>
</tr>
<tr>
<td>senderVM</td>
<td>UCascadeSelect</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h4>@change</h4>
<p>选择值改变时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.value</td>
<td>Any</td>
<td>改变后的值</td>
</tr>
<tr>
<td>$event.oldValue</td>
<td>Any</td>
<td>旧的值</td>
</tr>
<tr>
<td>$event.values</td>
<td>Array</td>
<td>改变后每项值的数组</td>
</tr>
<tr>
<td>$event.oldValues</td>
<td>Array</td>
<td>旧的每项值的数组</td>
</tr>
<tr>
<td>senderVM</td>
<td>UCascadeSelect</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
</u-article></template>

