module.exports = "<template><u-article><h1>UNumberInput 数字输入</h1>
<h2>示例</h2>
<h3>基本形式</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-number-input.vue/README.md">
    <div><u-linear-layout>
    <u-number-input></u-number-input>
    <u-number-input :value="6"></u-number-input>
</u-linear-layout></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"6"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>双向绑定</h3>
<u-code-example
    :show-code="false"
    :show-detail="true"
    file-path="src/components/u-number-input.vue/README.md">
    <div><anondemo-7ac96e6a-182 /></div>
    <div slot="code"><pre v-pre class="hljs lang-vue"><code><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">value</span>: <span class="hljs-number">6</span>,
        };
    },
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>最大值和最小值</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-number-input.vue/README.md">
    <div><u-linear-layout>
    <u-number-input :min="4" :max="12"></u-number-input>
    <u-number-input :value="6" :min="4" :max="12"></u-number-input>
</u-linear-layout></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"4"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"12"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"6"</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"4"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"12"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>精度与间隔</h3>
<p>精度表示数字要保留的最小单位，整数、小数均可，输入框会根据它作四舍五入。默认为<code>1</code>，即保留到整数。</p>
<p>间隔表示点击按钮或按上下键所增加或减少的量，最好为精度的整数倍。</p>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-number-input.vue/README.md">
    <div><u-linear-layout>
    <u-number-input :value="6" :precision="0.1" :step="0.5"></u-number-input>
    <u-number-input :value="5" :precision="5" :step="10"></u-number-input>
    <u-number-input :value="6" :step="10"></u-number-input>
</u-linear-layout></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"6"</span> <span class="hljs-attr">:precision</span>=<span class="hljs-string">"0.1"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"0.5"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"5"</span> <span class="hljs-attr">:precision</span>=<span class="hljs-string">"5"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"10"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"6"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"10"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>错误修复时机</h3>
<p>用<code>fixOn</code>属性可以设置错误在失焦时修复（默认）还是在输入时修复。</p>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-number-input.vue/README.md">
    <div><u-linear-layout>
    <u-number-input :value="20" :precision="10" :step="10"></u-number-input>
    <u-number-input :value="20" :precision="10" :step="10" fix-on="input"></u-number-input>
</u-linear-layout></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"20"</span> <span class="hljs-attr">:precision</span>=<span class="hljs-string">"10"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"10"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"20"</span> <span class="hljs-attr">:precision</span>=<span class="hljs-string">"10"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"10"</span> <span class="hljs-attr">fix-on</span>=<span class="hljs-string">"input"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>格式化</h3>
<p>通常可以用以下字符组成一个格式化字符串：</p>
<ul>
<li><code>0</code>表示数字占位符。如果整数部分长度小于占位符的数量，则用<code>0</code>补足。如果小数部分长度大于占位符数量，则会四舍五入。</li>
<li><code>#</code>表示数字占位符。只显有意义的零而不显示无意义的零。</li>
<li><code>.</code>表示小数点。</li>
<li><code>,</code>表示千分位。</li>
</ul>
<p>也可以传入一个包含<code>get</code>和<code>set</code>方法的格式化对象。</p>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-number-input.vue/README.md">
    <div><u-linear-layout>
    <u-number-input :value="20" formatter="0000"></u-number-input>
    <u-number-input :value="1234" :precision="0.01" :step="0.1" formatter="$ #,##0.00"></u-number-input>
    <u-number-input :value="20" :precision="0.5" :step="0.5" formatter="0.0%"></u-number-input>
</u-linear-layout></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"20"</span> <span class="hljs-attr">formatter</span>=<span class="hljs-string">"0000"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"1234"</span> <span class="hljs-attr">:precision</span>=<span class="hljs-string">"0.01"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"0.1"</span> <span class="hljs-attr">formatter</span>=<span class="hljs-string">"$ #,##0.00"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"20"</span> <span class="hljs-attr">:precision</span>=<span class="hljs-string">"0.5"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"0.5"</span> <span class="hljs-attr">formatter</span>=<span class="hljs-string">"0.0%"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h3>只读和禁用</h3>
<u-code-example
    :show-code="true"
    :show-detail="false"
    file-path="src/components/u-number-input.vue/README.md">
    <div><u-linear-layout>
    <u-number-input :value="6" readonly></u-number-input>
    <u-number-input :value="6" disabled></u-number-input>
</u-linear-layout></div>
    <div slot="code"><pre v-pre class="hljs lang-html"><code><span class="hljs-tag">&lt;<span class="hljs-name">u-linear-layout</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"6"</span> <span class="hljs-attr">readonly</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">u-number-input</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"6"</span> <span class="hljs-attr">disabled</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">u-number-input</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">u-linear-layout</span>&gt;</span></code></pre>
</div>
</u-code-example>

<h2>API</h2>
<h3>Props/Attrs</h3>
<table>
<thead>
<tr>
<th>Prop/Attr</th>
<th>Type</th>
<th>Default</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>value.sync, v-model</td>
<td>Number</td>
<td><code>0</code></td>
<td>输入框的值</td>
</tr>
<tr>
<td>min</td>
<td>Number</td>
<td><code>-Infinity</code></td>
<td>最小值</td>
</tr>
<tr>
<td>max</td>
<td>Number</td>
<td><code>+Infinity</code></td>
<td>最大值</td>
</tr>
<tr>
<td>step</td>
<td>Number</td>
<td><code>1</code></td>
<td>间隔，表示点击按钮或按上下键所增加或减少的量</td>
</tr>
<tr>
<td>precision</td>
<td>Number</td>
<td><code>1</code></td>
<td>精度，表示数字要保留的最小单位，整数、小数均可</td>
</tr>
<tr>
<td>formatter</td>
<td>String, Object</td>
<td></td>
<td>格式化字符串，具体参见示例。也可以传入一个包含<code>get</code>和<code>set</code>方法的格式化对象。</td>
</tr>
<tr>
<td>fix-on</td>
<td>String</td>
<td><code>'blur'</code></td>
<td>错误修复时机。可选值：<code>'input'</code>表示在输入时修复，<code>'blur'</code>表示在失焦时修复</td>
</tr>
<tr>
<td>placeholder</td>
<td>String</td>
<td></td>
<td>原生属性</td>
</tr>
<tr>
<td>autofocus</td>
<td>Boolean</td>
<td></td>
<td>原生属性</td>
</tr>
<tr>
<td>hide-buttons</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否隐藏按钮</td>
</tr>
<tr>
<td>readonly</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否只读</td>
</tr>
<tr>
<td>disabled</td>
<td>Boolean</td>
<td><code>false</code></td>
<td>是否禁用</td>
</tr>
<tr>
<td>size</td>
<td>String</td>
<td><code>'normal'</code></td>
<td>大小扩展，支持一个值：<code>'mini'</code>, <code>'small'</code>, <code>'normal'</code>, <code>'large'</code>, <code>'huge'</code>, <code>'full'</code>，或两个值的组合，前者表示高度，后者表示宽度，类似CSS的padding书写格式</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<h4>@input</h4>
<p>输入时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event</td>
<td>String</td>
<td>输入框的值</td>
</tr>
<tr>
<td>senderVM</td>
<td>UNumberInput</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h4>@change</h4>
<p>值变化时触发（与原生事件不同）</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event.value</td>
<td>Number</td>
<td>改变后的值</td>
</tr>
<tr>
<td>$event.oldValue</td>
<td>Number</td>
<td>旧的值</td>
</tr>
<tr>
<td>$event.formattedValue</td>
<td>String</td>
<td>格式化后的值</td>
</tr>
<tr>
<td>$event.valid</td>
<td>Boolean</td>
<td>改变后的值是否合法</td>
</tr>
<tr>
<td>senderVM</td>
<td>UNumberInput</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h4>@focus</h4>
<p>获得焦点时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event</td>
<td>String</td>
<td>原生事件对象</td>
</tr>
<tr>
<td>senderVM</td>
<td>UNumberInput</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
<h4>@blur</h4>
<p>失去焦点时触发</p>
<table>
<thead>
<tr>
<th>Param</th>
<th>Type</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td>$event</td>
<td>String</td>
<td>原生事件对象</td>
</tr>
<tr>
<td>senderVM</td>
<td>UNumberInput</td>
<td>发送事件实例</td>
</tr>
</tbody>
</table>
</u-article></template>

<script>
    import Component0 from "./anondemo-7ac96e6a-182.vue";

    export default {
        components: {
'anondemo-7ac96e6a-182': Component0,
},
    }
</script>
            "


